import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime';
import 'dayjs/locale/zh-cn';
dayjs.locale('zh-cn');
dayjs.extend(relativeTime);
export default {
    name: 'ArticleList',
    props: {
        data: {
            type: Array,
            default: () => []
        },
        loading: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            articleList: [
                {
                    cover: require('~/assets/images/background.png'),
                    tags: ['喵喵喵', '汪汪汪', '呱呱呱', 'Games'],
                    date: '2019-12-02',
                    _id: 'abcefg'
                },
                {
                    cover: require('~/assets/images/background-2.jpg'),
                    tags: ['喵喵喵', '汪汪汪', '呱呱呱', 'Games'],
                    date: '2019-12-02',
                    _id: 'abcefg'
                }
            ]
        };
    },
    methods: {
        formatDate(date, params) {
            if (params) {
                return dayjs(date).format(params);
            } else {
                return dayjs().from(dayjs(date));
            }
        }
    }
};
