import Loading from '@/components/Loading';
import ArticleList from '@/components/ArticleList';
import Pagination from '@/components/Pagination';
import Error from '@/components/Error';
export default {
    name: 'PageAuthor',
    components: {
        Pagination,
        ArticleList,
        Loading,
        Error
    },
    head() {
        return Object.assign(this.$nuxtI18nSeo(), {
            title: `${
                this.params.page
                    ? `${this.$t('page', { page: this.params.page })} - `
                    : ''
            }作者 ${this.params.id} 下的文章 - ${process.env.title}`
        });
    },
    data() {
        return {
            pagination: parseInt(this.$route.params.page) || 1,
            loading: false,
            error: null
        };
    },
    computed: {
        rootUrl() {
            return `/author/${this.params.id}`;
        },
        params() {
            const params = this.$route.params;
            return {
                page: params.page,
                id: params.id
            };
        }
    },
    watch: {
        pagination(page) {
            if (page < 1) {
                return false;
            }
            if (page > this.$store.state.data.posts.total_pages) {
                return false;
            }
            if (page > 1) {
                this.$router.push(
                    this.localePath(`${this.rootUrl}/${page}.html`)
                );
            } else {
                this.$router.push(this.localePath(`${this.rootUrl}.html`));
            }
        }
    },
    fetch({ store, params, redirect }) {
        if (process.server) {
            if (parseInt(params.page) === 1) return redirect('/');
            const page = parseInt(params.page) || 1;
            store.commit('SET_STORE', {
                key: 'ssr_init',
                value: true
            });
            return store.dispatch('getPosts', {
                page,
                author: params.id.replace('.html', '')
            });
        }
    },
    mounted() {
        if (this.$store.state.ssr_init) return this.$store.commit('resetInit');
        this.loading = true;
        this.error = null;
        this.$store
            .dispatch('getPosts', {
                page: this.pagination,
                author: this.$route.params.id
            })
            .then((e) => {
                this.loading = false;
            })
            .catch((err) => {
                this.error = err;
                this.loading = false;
            });
    },
    methods: {
        handleLink(page) {
            if (page <= 1) {
                return `${this.rootUrl}.html`;
            } else {
                return `${this.rootUrl}/${page}.html`;
            }
        },
        getData(page) {
            if (page < 1) {
                return false;
            }
            if (page > this.$store.state.data.posts.total_pages) {
                return false;
            }
            this.pagination = page;
        }
    }
};
